
ul{
    list-style:none;
    width: 50%;
    display: flex;
    justify-content: center;
    padding: 0;
    margin: 0 auto;
    margin-top: 10%;
    li{
        width: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        div{
            background-color: rgb(255, 255, 255);
            width:150px;
            height:150px;
            border-radius: 100px;
            border: 1px solid black;
          }
          p{margin: 0;}
    }
}